import D from '../../DispatchAction'
import {generate as id} from "shortid"
import imgUser from '../../../img/imgUser/User_Img.jpg'
import {userAPI} from '../../../api/api'
const stateHome = {
        data : {
        },
        isLoading: false,
        Posts:[
          {
            message: 'asdasda',
            likesCounts: 121,
            id: id(),
          }, {
            message: 'asdasda',
            likesCounts: 0,
            id: id(),
          },{
            message: 'asdasda',
            likesCounts: 2,
            id: id(),
          }
        ],
        status: ''

}


const HomeReduser = (state=stateHome, action) => {
    switch (action.type) {
        case D.ADD_NEW_POST:
        let newPost = {
            message: action.newText,
            likesCounts: 0,
            id: id(),
            img:state.data.photos.largea
        }
        return {
            ...state, 
            Posts: [...state.Posts,newPost]
        }
        case D.H_OPEN_USER_HOME_PAGE: 
          return{
           ...state, data: action.data
          }
          case D.S_LOADING_FALSE :
            return{
                ...state, isLoading: false
            }
        case D.S_LOADING_TRUE :
            return{
                ...state, isLoading: true
            }
        case D.SET_STATUS :
          return {
            ...state,
            status: action.status

          }
        default: return state;
    }

}

export default HomeReduser

export const R_FU_HOME_addNewPostRed = (text) => ({
  type: D.ADD_NEW_POST, newText:text
})

export const R_FU_HOME_OPEN_USER_HOME_PAGE =  obj => ({
type: D.H_OPEN_USER_HOME_PAGE, data:obj
})
export const R__FU_H_IsLoading_True = () => ({
  type: D.S_LOADING_TRUE
})
export const R__FU_H_IsLoading_False = () => ({
  type: D.S_LOADING_FALSE
})
export const R__FU_H_Set_Status = status => ({
  type: D.SET_STATUS, status
})


export const getProfile = userId => dispatch => {
    dispatch(R__FU_H_IsLoading_False())
    userAPI.getProfile(userId)
      .then(response=>{
        dispatch(R_FU_HOME_OPEN_USER_HOME_PAGE(response.data))
        dispatch(R__FU_H_IsLoading_True())
   })
};
///////////////////
export const getStatus = id =>dispatch => {
    dispatch(R__FU_H_IsLoading_False())
    userAPI.getStatusUser(id)
      .then(response=>{
        // console.log(response)
        dispatch(R__FU_H_Set_Status(response.data))
        dispatch(R__FU_H_IsLoading_True())

      })
     
  }
export const updateStatus = status =>dispatch => {
    dispatch(R__FU_H_IsLoading_False())
    userAPI.updateStatus(status)
      .then(response=>{
        if(response.data.resultCode === 0) {
        dispatch(R__FU_H_Set_Status(status))
        }
        dispatch(R__FU_H_IsLoading_True())

      })
  }

  ///////////////////////