import D from '../../DispatchAction'
import {userAPI} from '../../../api/api'

let UsersObj = {
    Users: [],
  pageSize: 10,
  totalUsersCount: 0,
  currentPage: 1,
  isLoadingSearch: false,
  followingPogres: []
}





const SearchReduser = (state=UsersObj, action) => {
    switch (action.type) {
      case D.S_FOLLOW_USER: 
return {...state, 
    Users: state.Users.map(u=>{
        if(u.id === action.id) {
            return{...u,followed:true }
        }
        return u
    })
}
      case D.S_UNFOLLOW_USER: 
return {...state, 
    Users: state.Users.map(u=>{
        if(u.id === action.id) {
            return{...u,followed:false }
        }
        return u
    })
}
    case D.S_SET_USERS:
        return{
            ...state, Users: action.users
        }
    case D.S_CURRENT_PAGE:   
    return {
        ...state, currentPage: action.currentPage
    } 
    case D.S_SET_TOTLA_USERS_COUNT : 
    return{
        ...state, totalUsersCount: action.currentTotalCount
    }
    case D.S_LOADING_FALSE :
        return{
            ...state, isLoadingSearch: false
        }
    case D.S_LOADING_TRUE :
        return{
            ...state, isLoadingSearch: true
        }
    case D.S_IS_FOLOWING_PRORES : 
    return {
        ...state, 
        followingPogres: action.isfollowingPogres 
        ? [...state.followingPogres, action.userId]
        : state.followingPogres.filter(id=> id != action.userId)
    }
        default: return state;
    }
    
}

export default SearchReduser

export const R_FU_isfollowingPogres = (isfollowingPogres, userId) => ({
    type: D.S_IS_FOLOWING_PRORES, isfollowingPogres , userId
})

export const R_FU_Follow = id=>({
   type: D.S_FOLLOW_USER,
    id
})
export const R_FU_Unfollow = id=>({
   type: D.S_UNFOLLOW_USER,
   id
})

export const R_FU_SetUsers = users => ({
    type:D.S_SET_USERS, users
})

export const R__FU_CURRENT_PAGE = currentPage =>({
    type: D.S_CURRENT_PAGE, currentPage
})

export const R__FU_S_SET_TOTLA_USERS_COUNT = currentTotalCount =>({
    type: D.S_SET_TOTLA_USERS_COUNT, currentTotalCount
})

export const R__FU_S_IsLoading_True = () => ({
    type: D.S_LOADING_TRUE
})
export const R__FU_S_IsLoading_False = () => ({
    type: D.S_LOADING_FALSE
})



export const getUsersThanksCreator = (currentPage,pageSize) =>{
   return dispatch => {
        dispatch(R__FU_S_IsLoading_False())

        userAPI.getUsers(currentPage, pageSize)
    
        .then(response => {
        dispatch(R_FU_SetUsers(response.items))
        dispatch(R__FU_S_SET_TOTLA_USERS_COUNT(response.totalCount))
        dispatch(R__FU_S_IsLoading_True())
        dispatch(R__FU_CURRENT_PAGE(currentPage))
    })
}}


export const unfollow_Thank = userId =>{
   return dispatch => {
    dispatch(R_FU_isfollowingPogres(true, userId))

    userAPI.unfollow_delete(userId)
    .then(response => {
        if(response.resultCode === 0) {
            dispatch(R_FU_Unfollow(userId))
        }
    
    })
    .finally( dispatch(R_FU_isfollowingPogres(false, userId)))
}}


export const follow_Thank = (userId) =>{
   return dispatch => {
    dispatch(R_FU_isfollowingPogres(true, userId))

  userAPI.follow_post(userId)
  .then(response => {
      if(response.resultCode === 0) {
        dispatch(R_FU_Follow(userId))
      }
     
  })

    .finally( dispatch(R_FU_isfollowingPogres(false, userId)))
}}