import React, { useEffect, useState } from 'react'
import S from './SearchUser.module.css'
import imgUser from '../../../img/imgUser/User_Img.jpg'
import { NavLink } from 'react-router-dom'
const SearchUser = ({
    User,
    followingPogres,
    follow_Thank,
    unfollow_Thank
}) => {
    const MainimgUser =  User.photos.small  ? User.photos.small : imgUser 
    return(
        <div id={User.id}>
         <NavLink to={'/Home/'+User.id}><img src={MainimgUser} alt="" className={S.imgUser}/></NavLink>
           <div>{User.name}</div> 

           {User.followed 

?<button
 disabled={followingPogres.some(id=> id === User.id)}
 onClick={()=>{
  unfollow_Thank(User.id)
}}>Unfollow</button>


: <button 
disabled={followingPogres.some(id=> id === User.id)} 
onClick={()=>{
  follow_Thank(User.id)
}}>Follow</button>

}
            <div>
            {/* <span>Like</span> {Post.likesCounts} */}
            </div>
        </div>
    )
}
export default SearchUser