import React from 'react'
import { NavLink, Route } from 'react-router-dom'
import s from './NamePerson.module.css'
import MessageDialog from './MessageDialogs'


const NamePerson = props => {
    return(
        <div className={s.container}>
        <div className={s.namePersonContainer}>
        <NavLink to={`/Message/${props.name}`} 
 className={s.itemName + ' ' + s.active}>{props.name}</NavLink>
        </div>
        <div className={s.messagePersonContainer}>
 <Route path={`/Message/${props.name}`} render={()=><MessageDialog 
 UserMess={props.UserMess} 
 newMessageText={props.newMessageText} 
  P_F_SendNewMessagge={props.P_F_SendNewMessagge}
   P_F_Message_newMessageText={props.P_F_Message_newMessageText}
   id={props.id}
   />}/> 
        </div>
        </div>
 
    )
}
export default NamePerson