import React from 'react'
import s from './Post.module.css'


const Post= ({
    Post,
    UserImg
}) => {
    return(
        <div id={Post.id}>
            <img src={UserImg} alt="" className={s.img}/>
            {Post.message}
            <div>
            <span>Like</span> {Post.likesCounts}
            </div>
        </div>
    )
}
export default Post