
import D from './DispatchAction'
import {userAPI} from "../api/api"

const initiaState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    isLoading: false

}


const auther_reducer = (state=initiaState, action) => {
    switch (action.type) {
    case D.ATHE_SET_USER_DATA :
          return{
              ...state,
              ...action.data,
              isAuth: true
          }
    case D.LOADING_FALSE :
            return{
                ...state, isLoading: false
            }
    case D.LOADING_TRUE :
            return{
                ...state, isLoading: true
            }


        default: return state;
    }

}

export const IsLoading_True = () => ({
    type: D.LOADING_TRUE
})

export const IsLoading_False = () => ({
    type: D.LOADING_FALSE
})

export const A_set_User_Data = (userId,email,login) => ({
type : D.ATHE_SET_USER_DATA, data: {userId, login, email}
})


export default auther_reducer

export const getLogin = () => {
  return dispatch => {

    dispatch(IsLoading_False())
       
    // Axios.get(`https://social-network.samuraijs.com/api/1.0/auth/me`, {
    //  withCredentials: true
    // })
    userAPI.getLogin()
    .then(response => {
        if(response.data.resultCode === 0){
          let {id, email, login} = response.data.data
          dispatch(A_set_User_Data(id, email, login))
        }
        dispatch(IsLoading_True())
   })
  }  
}